@import "../../App.scss";

.contact {
  height: 100%;
  max-width: 1300px;
  margin: auto;
  display: flex;
  align-items: center;
  gap: 50px;

  @include mobile {
    width: 100%;
    padding: 10px;
    flex-direction: column;
    gap: 5px;
  }

  .contact-container--text {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include mobile {
      gap: 10px;
      text-align: center;
      align-items: center;
      flex: 0.5;
    }

    h1 {
      font-size: 100px;
      line-height: 88px;

      @include mobile {
        font-size: 24px;
        line-height: 20px;
      }
    }

    h2 {
      @include mobile {
        font-size: 15px;
      }
    }

    span {
      font-weight: 300;

      @include mobile {
        font-size: 14px;
      }
    }
  }

  .contact-container--form {
    flex: 1;
    position: relative;

    .phone-svg {
      //background-color: rebeccapurple; - use for test
      stroke: orange;
      position: absolute;
      margin: auto;
      width: 450px;
      height: 450px;

      @include mobile {
        width: 200px;
        height: 200px;
      }
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 20px;

      input,
      textarea {
        padding: 20px;
        background-color: transparent;
        border: 1px solid white;
        color: white;
        border-radius: 5px;
        z-index: 1;
      }

      button {
        padding: 20px;
        border: none;
        background-color: orange;
        cursor: pointer;
        font-weight: 500;
        transition-duration: 0.2s;
        transition-property: scale;
        z-index: 1;

        &:hover {
          background-color: transparent;
          border: 1px solid orange;
          color: orange;
        }
        &:active {
          scale: 0.9;
        }

        @include mobile {
          padding: 10px;
        }
      }
    }
  }
}
