/* Color Palette and Typography: Start */
/* End: Color Palette and Typography */
.navbar {
  height: 100px;
}
.navbar .navbar-wrapper {
  max-width: 1300px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
@media (max-width: 738px) {
  .navbar .navbar-wrapper {
    justify-content: flex-end;
    padding: 20px;
  }
}
.navbar .navbar-wrapper .navbar-brand {
  font-weight: 600;
}
@media (max-width: 738px) {
  .navbar .navbar-wrapper .navbar-brand {
    display: none;
  }
}
.navbar .navbar-wrapper .navbar-social {
  display: flex;
  gap: 20px;
}
.navbar .navbar-wrapper .navbar-social img {
  width: 18px;
  height: 18px;
